#player {
  display: flex;
  flex-direction: column;
}

#play-controls {
  display: flex;
  flex-direction: column;
  .controls-row {
    display: flex;
    flex-direction: row;
    justify-content: center;

    & .MuiSlider-markLabel {
      visibility: hidden;
      @media (min-width: 960px) {
        visibility: visible;
      }
    }
  }
}
