.visualizer-with-channel-display {
  // Oof that name
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.visualization-container {
  height: 100%;
  width: 100%;
  position: relative;
  flex: 1;
}

.channel-enable {
    max-height: 32px;
    overflow: hidden;
    & > div {
        // Hack into the button group immediately following.
        // Terrible way to hide the second button but whatever, it's natural.
        flex-wrap: wrap;
    }
}

.channel-status {
  //display: contents;

  display: flex;
  flex-wrap: nowrap;
  margin-left: 16.666666%;
  margin-right: 16.666666%;

  & > div {
    opacity: 0.2;
    color: darkgray;
    flex: 1;

    &.enabled {
      opacity: 1;
      text-shadow: 0em 0em 0.075em black;
      color: white;
    }

    &.channel-echo.enabled {
      background-color: green;
    }

    &.channel-noise.enabled {
      background-color: orange;
    }

    &.channel-pmod.enabled {
      background-color: purple;
    }
  }
}
