.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

canvas {
  border: 1px solid black;
}

.channel-display-container {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
}

.scroll-vertical {
  & .channel-status {
    display: flex;
    flex-wrap: nowrap;
  }

  & .channel-volume {
    flex-direction: row;
    flex-wrap: nowrap;
  }

  & .channel-left .channel-volume-fill {
    left: calc(100% - var(--volumeL));
  }

  & .channel-right .channel-volume-fill {
    right: calc(100% - var(--volumeR));
  }

  & .channel-volume-breaker {
    padding-left: 3px;
    padding-right: 3px;
    border: 1px solid gray;
  }
}

.player-controls {
  display: flex;
  flex-direction: column;
}

.spc-player {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.song-selector-fab {
  position: fixed !important;   // ok
  bottom: 24px;
  right: 24px;
  z-index: 99999987654321;
}

@media (orientation: portrait) {
  
}

@media (orientation: landscape) {
  
}



.channel-display {
  font-family: monospace !important; // lol

  display: flex;
  flex: 1;

  .channel-left, .channel-right {
    position: relative;
  }

  & .channel-volume-fill {
    background-color: green;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  
  & .channel-left, & .channel-right {
    flex: 1;
  }

  .channel-volume {
    display: flex;
  }

  .scroll-to-bottom & {
    flex-direction: column-reverse;
  }

  .scroll-to-top & {
    flex-direction: column;
  }

  .scroll-to-left & {
    flex-direction: row-reverse;
  }

  .scroll-to-right & {
    flex-direction: row;
  }

  transition: all 0.25s;

  

  .channel-pitch {
    display: flex;
    flex-direction: column;
    padding-left: 1em;
  }
}

.scroll-horizontal {
  position: relative;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
